import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Slide } from 'ionic-angular';
import { SlideCategory, Collections } from '../../app/app.models';
import { Observable } from 'rxjs';
import { AngularFirestore } from 'angularfire2/firestore';
import { map, tap } from 'rxjs/operators';

@IonicPage()
@Component({
  selector: 'page-slides',
  templateUrl: 'slides.html',
})
export class SlidesPage {

  public slides$: Observable<any[]>

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
  ) {
  }

  ionViewDidLoad() {
    this.slides$ = this.afs.collection<SlideCategory>(Collections.SLIDE_CATEGORY)
      .valueChanges()
      .pipe(
        map(cats => cats.map(cat => ({ ...cat, children$: this.afs.collection<Slide>(Collections.SLIDE, ref => ref.where('cat_id', '==', cat.id)).valueChanges() }))),
        tap(console.log)
      )
  }

}



